import {connect} from "react-redux";
import React, {Component} from "react";
import PropTypes from "prop-types";
import {getCvCheckoutErrors} from "../../selectors";
import {dismissCvErrors, gotoPreviousCheckoutStep} from "../../actions/app";
import {OraButton} from "eshop/components/OraCommonComponents";
import OraModal from "eshop/modules/core/components/ui/Modal";
import {getCartFirstOffer,getOfferMsisdn,getOfferLoyaltyLength,getCartCode,getCartDevices,getMaxBundleNo } from "eshop/modules/cart/selectors"
import OnlineUtils from "eshop/utils/OnlineUtils";
import {removeFromCartAndRedirect, handleRepayment} from "eshop/modules/cart/actions/cart";

class CustomerVerificationModalView extends Component{
    constructor(props){
        super(props);
        this.state =  {shouldClose: false, checkboxAccepted: false, showPaymentWarning: false};
    }


    handleCancelOrder() {
        this.props.removeFromCartAndRedirect();
    }

    handleRepayment(){
        if(!this.state.checkboxAccepted){
            this.setState({showPaymentWarning: true});
        }else{
            this.props.handleRepayment();
        }
    }
    componentWillReceiveProps(nextProps) {
        // if all bundles are gone
        if(this.props.noOfBundles > 0 && (nextProps.noOfBundles == null || !nextProps.noOfBundles)) {
            this.setState({shouldClose : true})
            }
     }

     componentDidUpdate() {
     if (this.state.shouldClose) {
        this.setState({shouldClose : false})
        // redirect to cart summary
        this.props.action();
     }
     }
    handleCheckboxChange(event){
        this.setState({checkboxAccepted : event.target.checked});
        this.setState({showPaymentWarning : !event.target.checked});
    }

    isPaytelAcceptable(){
        return (this.props.channels.sales=='POS' || this.props.channels.sales=='AKA') && this.props.errors.every((cvVer) => {return !!cvVer.paytelAcceptable});

    }
    renderInfo(){
        return (<span>Dokonaliśmy weryfikacji Twojego zamówienia. <br/>Niestety nie możemy go zrealizować. Zobacz szczegóły poniżej.</span>)
    }

    renderSeparatorLine(){
                return (
                    <tr>
                        <td className="o-separator u-padding-top-l" colSpan="2"/>
                    </tr>
                )
    }

    renderLoyaltyLength() {
        switch (this.props.loyaltyLength) {
            case 0:
                return (<div>czas nieokreślony</div>);
            case 12:
                return (<div>12 miesięcy</div>);
            case 24:
                return (<div>24 miesiące</div>);
            default:
                return (<div>inny</div>);
        }
    }

    renderErrorTable(error, descriptions, isPos, showPaymentWarning){
        let thClassName = "u-padding-top-l u-padding-right";
        let tdClassName = "u-font-bold u-padding-top-l u-padding-left";
        return (
                <tbody>
                    {error.details.msisdn &&
                        <tr>
                            <th className={thClassName}>
                                { descriptions.msisdnCaption ||"Numer telefonu"}
                            </th>
                            <td className={tdClassName}>
                                {OnlineUtils.formatMsisdn(error.details.msisdn)}
                            </td>
                        </tr>
                    }
                    {error.details.planName &&
                        <tr className="">
                            <th className={thClassName}>
                                {descriptions.planCaption || "Plan taryfowy"}
                            </th>
                            <td className={tdClassName}>
                                {error.details.planName}
                            </td>
                        </tr>
                    }
                    <tr className="">
                        <th className={thClassName}>
                            {descriptions.loyaltyCaption || "Okres trwania umowy"}
                        </th>
                        <td className={tdClassName}>
                            {this.renderLoyaltyLength()}
                        </td>
                    </tr>
                    {error.details.deviceName && [
                    <tr className="">
                        <th className={thClassName}>
                            {descriptions.deviceCaption || "Zamawiane urządzenie"}
                        </th>
                        <td className={tdClassName}>
                            {error.details.deviceName }
                        </td>
                    </tr>
                    ] }
                    <tr className="">
                        <td className="u-padding-top-l" colSpan="2">
                                <strong>{descriptions.declineReasonCaption || "Powód odmowy:"} </strong>
                            {error.code || descriptions.defaultMessage || "Brak dodatkowych informacji. [Default msg, set descriptions.defaultMessage]"}
                        </td>
                    </tr>
                    {isPos &&
                     <tr className="">
                        <td className="u-padding-top-l" colSpan="2">
                         <label className="o-checkbox opl-checkbox opl-checkbox--black">
                            <input type="checkbox" onChange={event => {this.handleCheckboxChange(event)}}/> <span className="o-ci"></span>
                               <span className="o-ci-label"><strong>{descriptions.continuePaytel || "Kontynuuj zamówienie ze spłatą zadłużenia"} </strong></span>
                         </label>
                        </td>
                    </tr>}
                     {isPos &&
                     <tr className="">
                        <td colSpan="2" className="u-padding-top-s" >
                                {"Zaznacz powyższe i kontynuuj jeśli Klient zgadza się na dokonanie spłaty zadłużenia na miejscu przez terminal Paytel"}
                        </td>
                    </tr>}
                    {isPos && showPaymentWarning &&
                     <tr className="">
                        <td className="u-padding-top-l" colSpan="2">
                        <div className="o-icon-text g-icon g-icon--error g-icon--before g-icon--xs-s g-redf-c ">
                            <p className="o-icon-text__text u-font-bold opl-msg--error">Akceptacja jest wymagana do realizacji zamówienia.</p>
                        </div>
                        </td>
                     </tr>
                    }
                    {this.renderSeparatorLine()}
                </tbody>
            )
     }

    render() {
        return (
                <OraModal showClose={true} open={this.props.errors && this.props.errors.length>0} onClose={this.props.dismiss} size="narrow">

                        <h2 className="u-font-standard u-margin-l opl-modal-title-space">
                                <span className="h3 u-small-hide">
                                    {this.props.descriptions.headerCaption?this.props.descriptions.headerCaption.replace("{}",this.props.cartCode) : ("Zamówienie nie może być zrealizowane")}
                                </span>
                                <span className="h1 u-large-hide u-medium-hide">
                                    {this.props.descriptions.headerCaption?this.props.descriptions.headerCaption.replace("{}",this.props.cartCode) : ("Zamówienie nie może być zrealizowane")}
                                </span>
                        </h2>
                            <p className="u-margin-l">
                                {this.props.descriptions.infoCaption || this.renderInfo()}
                            </p>

                                <h2 className="u-font-standard u-no-margin">
                                    <span className="h4 u-small-hide">
                                        {this.props.descriptions.declineReasonHeader || "Negatywnie zweryfikowane:"}
                                    </span>
                                    <span className="h2 u-large-hide u-medium-hide">
                                        {this.props.descriptions.declineReasonHeader || "Negatywnie zweryfikowane:"}
                                    </span>
                                </h2>

                        <table className="u-table-fixed">
                            <caption className="u-acc-hide">
                                <h3>Lista niedostępnych produktów</h3>
                            </caption>

                            {this.props.errors.map( (error, i) =>
                                this.renderErrorTable(error,this.props.descriptions,this.isPaytelAcceptable() && error.details.bundleNo===this.props.noOfBundles ,this.state.showPaymentWarning))
                            }

                        </table>

                        <div className="l-row u-margin-top-l">
                            {!this.props.action ? null :
                                !this.isPaytelAcceptable() ?
                                     [
                                      <div className="l-col l-col-small-12 l-col-medium-5 l-col-5 large-offset-by-3 medium-offset-by-3 u-small-padding-l">
                                         <OraButton onClick={this.handleCancelOrder.bind(this)} className="u-w-100" type="secondary">
                                             Anuluj zamówienie
                                         </OraButton>
                                     </div>,
                                     <div className="l-col l-col-small-12 l-col-medium-4 l-col-4 u-large-right u-medium-right">
                                         <OraButton onClick={this.props.action} className="u-w-100" type="primary">
                                             Wróć do koszyka
                                         </OraButton>
                                     </div>]
                                    :
                                    [
                                    <div className="l-col l-col-small-12 l-col-medium-5 l-col-5">
                                        <OraButton onClick={this.handleCancelOrder.bind(this)} className="u-w-100" type="secondary">
                                            Anuluj zamówienie
                                        </OraButton>
                                    </div>,
                                    <div className="l-col l-col-small-12 l-col-medium-4 l-col-4">
                                        <OraButton onClick={this.props.action} className="u-w-100" type="secondary">
                                            Wróć do koszyka
                                        </OraButton>
                                    </div>,
                                    <div className="l-col l-col-small-12 l-col-medium-3 l-col-3 u-large-right u-medium-right">
                                        <OraButton onClick={this.handleRepayment.bind(this)} className="u-w-100" type="primary">
                                            Kontynuuj
                                        </OraButton>
                                    </div>]
                            }
                        </div>
                </OraModal>
        )
    }



}

let mapStateToProps = state => ({
    errors: getCvCheckoutErrors(state),
    noOfBundles: getMaxBundleNo(state),
    offer : getCartFirstOffer(state),
    offerDescription:(getCartFirstOffer(state)?getCartFirstOffer(state).planName:""),
    deviceDescription:((getCartDevices(state)&& getCartDevices(state)[0])?getCartDevices(state)[0].description:null),
    device:getCartDevices(state),
    msisdn:getOfferMsisdn(state),
    loyaltyLength: getOfferLoyaltyLength(state),
    cartCode:getCartCode(state)
});
let mapDispatchToProps = dispatch=> ({
    dismiss: () => dispatch(dismissCvErrors()),
    action: () => dispatch(gotoPreviousCheckoutStep()),
    removeFromCartAndRedirect: () => dispatch(removeFromCartAndRedirect()),
    handleRepayment: () => dispatch(handleRepayment())
});

const CustomerVerificationModal = connect(mapStateToProps, mapDispatchToProps)(CustomerVerificationModalView);
export default CustomerVerificationModal;
